import React, { Component } from 'react';
import { updateIntraction } from "../../../../actions/themeActions";
import './reaction.scss'
class Like extends Component {
       state = { like: false };
       componentDidMount() {
              const { postId, wallID, interactiveCount, UserDetail } = this.props
              const dataLikes = localStorage.getItem(`intractivelike${wallID}`)
              if (dataLikes) {
                     const data = JSON.parse(dataLikes) || [];
                     if (data && data.length > 0) {
                            const likePost = data.filter((itm) => itm.cardId == postId);
                            if (likePost?.length > 0) {
                                   this.setState({ like: true })
                            }

                     }
              }

       }

       onClickLike = event => {
              event.stopPropagation();
              const { like } = this.state;
              this.setState({ like: !like }, () => this.onLikeSave());
       };

       onLikeSave = () => {
              const { postId, wallID, interactiveCount, UserDetail } = this.props
              const { like } = this.state
              const data = JSON.parse(localStorage.getItem(`intractivelike${wallID}`)) || [];
              if (like) {
                     const payload = {
                            "postId": postId,
                            "action": like ? "prepand" : "remove",
                            "wallId": wallID,
                            "table_name": UserDetail.db_table,
                     }
                     updateIntraction(payload).then((response) => {
                            var likeDataUpdate = []
                            if (like) likeDataUpdate = [...data, { count: interactiveCount + 1, cardId: postId, wallID: wallID }]
                            else likeDataUpdate = data.filter((itm) => itm.cardId != postId);
                            localStorage.setItem(`intractivelike${wallID}`, JSON.stringify(likeDataUpdate));
                     })
                            .catch((error) => {


                            });
              }

       }
       render() {
              const { like } = this.state;
              const { postId, likeClass } = this.props
              return <div className={`tb_like_ico${like ? ' tb_animate' : ''} ${likeClass}`} onClick={this.onClickLike} data-postid={postId}>
                     <div className={`tb__icon tb-${like ? 'heart' : 'heart-outline'}`}>
                            <div></div>
                     </div>
              </div>
       }
}

export default Like;
